<template>
  <div class="login-container">
    <div class="login-header">
      <div class="login-logo">
        <img src="../../images/login_logo.png" alt="" @click="tabclick('introducePage')">
        <img class="login-logo-line" src="../../images/login_header_line.png"> XPE 智能办公平台
      </div>
    </div>
    <div class="register-content-box">
      <div class="register-text">
        <h2>XPE 智能办公平台</h2>
        <h3>您的专属投资大管家</h3>
        <p>为PE投资机构量身打造的管理系统，帮助PE机构进行基金，项目，公司内部事务等全维度管理</p>
      </div>
      <div class="login-box register-box">
        <div class="title">
          注册XPE系统账号
        </div>
        <!-- 手机登录 -->
        <div class="register-content">
          <div class="login-Item">
            <div class="login-inputbox">
              <input type="input" placeholder="输入手机号" autocomplete="new-password" class="register-input" name="" v-model="phoneNum" @blur="checkPhonenum" maxlength='11'>
              <span class="line"></span>
            </div>
            <em class="error register-error">{{phoneNumErrorMsg}}</em>
          </div>
          <!-- 手机验证码 -->
          <div class="login-Item2">
            <div class="login-inputbox left">
              <input type="input" maxlength='6' placeholder="输入验证码" class="register-input register-input-code" name="" v-model="phoneNumVal" @blur="checkPhoneval">
              <span class="line"></span>
            </div>
            <input class="right register-code-btn" :class="{'register-code-btn-disabled1':phoneNumError||getPhoneCode||phoneNum==''}" type="button" :value="getPhoneCodeText" @click="getPhoneCodeClick">
            <em v-if="phoneNumValError" class="error">{{phoneNumValErrorMsg}}</em>
          </div>
        </div>
        <div>
          <input class="login-btn" :class="{'login-btn-disabled':loginOk}" type="button" value="注  册" @click="loginClick">
        </div>
        <div class="register-bottominfo">
          注册即代表同意<a href="http://pe.pewinner.com/uploads/xieyi/用户注册协议.pdf" target="_blank"> 《用户注册协议》</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    sendSaasCode,
    verifySaasCode
  } from '../../config/api'
  import {
    mapState,
    mapMutations
  } from 'vuex'
  import {
    dialogConfirm,
    isBroswer
  } from '../../config/mUtils'
  import {
    setStore,
    getStore,
    removeStore
  } from '../../config/mUtils'
  import api from '../../config/url.js'
  //import VueParticles from 'vue-particles'
  export default {
    name: 'HelloWorld',
    data() {
      return {
        phoneNum: '', //手机号
        phoneNumErrorMsg: '', //手机号格式错误提示内容
        phoneNumVal: '', //手机短信验证码
        phoneNumValErrorMsg: '', //手机验证码错误提示内容
        getPhoneCodeText: '获取验证码',
        getPhoneCode:false,
        timeout: ''
      }
    },
    components: {
      //VueParticles
    },
    created() {
      setStore("htcx_webtoken", ''); //存储本次token
      setStore("htcx_uid", ''); //uid
      api.resetServer();
      window.localStorage.removeItem('htcx_server_address');
      window.localStorage.removeItem('htcx_server_key');
      if(this.$route.query.phone != undefined){
        this.phoneNum = this.$route.query.phone;
      }
    },
    mounted() {},
    computed: {
      phoneNumError: function() {
        if (this.phoneNum != '') {
          return !(/^[1][3,4,5,7,8][0-9]{9}$/gi.test(this.phoneNum));
        } else {
          return false
        }
      },
      phoneNumValError: function() {
        if (this.phoneNumVal == '' || this.phoneNumVal.length == 6) {
          return false;
        } else {
          return true;
        }
      },
      //登录按钮是否可点击
      loginOk: function() {
        if (this.phoneNum != '' && this.phoneNumVal != '' && !this.phoneNumValError && !this.phoneNumError) {
          return true;
        } else {
          return false;
        }
      }
    },
    methods: {
      ...mapMutations([
        'RECORD_USERINFO', 'RECORD_USERURL', 'RECORD_USERNAME'
      ]),
      //检验手机号格式
      checkPhonenum() {
        if (this.phoneNumError) {
          this.phoneNumErrorMsg = '请输入正确的11位手机号码'
        } else {
          this.phoneNumErrorMsg = ''
        }
      },
      //检验手机验证码格式
      checkPhoneval() {
        if (this.phoneNumValError) {
          this.phoneNumValErrorMsg = '手机验证码格式错误'
        } else {
          this.phoneNumValErrorMsg = ''
        }
      },
      //注册按钮
      loginClick() {
        if(!this.loginOk){
          return false;
        }
        verifySaasCode({
          phone: this.phoneNum,
          code:this.phoneNumVal
        }).then(res => {
          if (res.data.msgNo == 1) {
          //  console.log(res.data.payload);
           let data = res.data.payload;
           if(data.is_finish==1){
             dialogConfirm('info','该账号已注册')
           }else{
            this.$router.push({
              name: 'registerInfo' ,
              query: {
                phone:data.phone ,
              },
            })
           }
          } else {
            let msg = res.data.message.split('错误码')[0];
            if (msg != undefined) {
              dialogConfirm('info', msg);
            }
          }
        })
      },
      getPhoneCodeClick() {
        if (!this.getPhoneCode&&this.phoneNumError == false && this.phoneNum != '') {
          // console.log(111);
          sendSaasCode({
            phone: this.phoneNum
          }).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '发送成功');
              var that = this;
              this.getPhoneCode = true;
              var num = 60;
              var timer = setInterval(function() {
                num--;
                that.getPhoneCodeText = num + '秒后重新获取';
                if (num === 0) {
                  that.getPhoneCodeText = '获取验证码';
                  that.getPhoneCode = false;
                  clearInterval(timer);
                }
              }, 1000)
            } else {
              let msg = res.data.message.split('错误码')[0];
              if (msg != undefined) {
                dialogConfirm('info', msg);
              }
            }
          })
        }else if(this.getPhoneCode){
          dialogConfirm('info', '发送太频繁啦')
        }
      },
      tabclick(name){
        // this.$router.push({
        //   name: name,
        // })
        window.location.href='http://www.pewinner.com';
        // window.location.href='/pe-introduce'
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../../style/login1';
  .login-code {
    position: relative;
  }
  
  .login-code-infoimg {
    position: absolute;
    left: -400px;
    top: -144px;
    transition: all 0.6s;
    animation: myfirst 1s;
  }
  
  .login-code-success {
    text-align: center;
    img {
      margin-top: 76px;
    }
    h2 {
      color: #282828;
      font-size: 24px;
      margin-top: 30px;
      line-height: 1em;
    }
    h3 {
      color: #A0A4AA;
      font-size: 14px;
      margin-top: 16px;
    }
  }
  
  @keyframes myfirst {
    from {
      opacity: 0;
      left: -420px
    }
    to {
      opacity: 1;
      left: -400px
    }
  }
</style>



